import * as React from 'react'

const wineImage = {
    width: '8rem'
}

export default function Wine() {
    /*
    1. Fetch from https://api.sampleapis.com/wines/reds
    2. Trsnsform into JSX
    */
    let items = []
    let [wineTitles, setWineTitles] = React.useState([])
    //let [coffeedescriptions, setCoffeeDescriptions] = React.useState([])

    React.useEffect(async () => {
        // Run once after the page finished loading
        // Fetch from https://api.sampleapis.com/wines/reds
        let res = await fetch('https://api.sampleapis.com/wines/reds')
        let wines = await res.json()
        for (let i = 0; i < wines.length; i++) {
            console.log(wines[i].title)
            items.push(<li>
                <div style={{ width: "40em", overflow: "hidden" }} >
                    <div style={{ width: "11rem", float: "left" }}> < img style={wineImage} src={wines[i].image} /> </div>
                    <div >

                        <b>{wines[i].winery}</b> -- {wines[i].wine}

                    </div>

                    <div>

                        Rating: Average {wines[i].rating.average} - Review {wines[i].rating.reviews}

                    </div>

                    <div>

                        Location: {wines[i].location}

                    </div>

                </div>
            </li>)
            // items.push(<li>{wines[i].description}</li>)
        }
        setWineTitles(items)
    }, [])

    return (
        <>
            <h1>Wine</h1>
            <ul>
                {wineTitles}

            </ul>
        </>
    )
}